
import { defineComponent, inject, ref, computed } from 'vue';
import { useRoute } from 'vue-router';
import { Datastore, Receipt } from '@/datastore';

interface DetailItem {
  itemID: number
  itemCode: string
  itemName: string
  itemUnitName: string
  itemPrice: number
  quantity: number
  discountPercentage: number
  discountAmount: number
  amount: number
}

export default defineComponent({
  setup () {
    const route = useRoute()
    const id = +route.params.id

    const datastore = inject<Datastore>('datastore')!
    const receipt = ref<Receipt>()
    datastore.receipt.getReceipt(id)
      .then(x => {
        receipt.value = x
      })

    const docNo = computed(() => receipt.value?.docNo)
    const date = computed(() => receipt.value?.date.toFormat('dd/MM/yyyy'))
    const detailItems = computed<DetailItem[]>(() => receipt.value?.details.map(x => ({
        itemID: x.itemID,
        itemCode: x.itemCode,
        itemName: x.itemName,
        itemUnitName: x.itemUnitName,
        itemPrice: x.itemPrice,
        quantity: x.quantity,
        discountPercentage: x.discountPercentage,
        discountAmount: x.discountAmount,
        amount: x.amount,
    })) ?? [])
    const subtotalBeforeDiscount = computed(() => receipt.value?.subtotalBeforeDiscount)
    const totalItemDiscount = computed(() => receipt.value?.totalItemDiscount)
    const subtotal = computed(() => receipt.value?.subtotal)
    const tradeDiscount = computed(() => receipt.value?.tradeDiscount)
    const grandTotal = computed(() => receipt.value?.grandTotal)

    return {
      docNo,
      date,
      detailItems,
      subtotalBeforeDiscount,
      totalItemDiscount,
      subtotal,
      tradeDiscount,
      grandTotal,
    }
  },
});
